<template>
    <div>
        <div id="box">
            <div class="divbox">
                <b> </b>
                <input v-model="storeName" class="input" style="margin-left: 30px"/><br>
                <br>
                <b> URL</b>
                <input v-model="storeURL" class="input" style="margin-left: 30px"/><br>
                <br>
                <b> </b>
                <textarea v-model="storeContent" class="input" style="margin-left: 30px; width:200px; height:100px"/><br>
            </div>
            <div class="buttons">
                <button v-if="isUpdate" class="btn" style="margin-right: 40px; margin-left: 40px" @click="updateStore"><b></b></button>
                <button v-else class="btn" style="margin-right: 40px; margin-left: 40px" @click="setStore"><b></b></button>
                <button class="btn" @click="cancle"><b></b></button>
            </div>
        </div>
    </div>
</template>

<script>
import http from "@/http.js";
export default {
    props:{
        islandpos:String,
        storepos:String,
        storeid:String,
    },
    data(){
        return{
            storeName: "",
            storeURL: "",
            userId: "",
            storeContent: "",
            islandPos : "",
            storePos : "",
            storeId: "",
            isUpdate: false,
        }
    },
    mounted(){
        if(this.islandpos != null){
            this.islandPos = this.islandpos
        }
        if(this.storepos != null){
            this.storePos = this.storepos
        }
        if(this.islandpos == null && this.storepos == null){
            this.storeId = this.storeid;
            this.isUpdate = true;
            //     
            http.get("/store/select/" + this.storeId)
            .then((res) => {
                this.storeName = res.data.storeName;
                this.storeContent = res.data.storeContent;
                this.storeUrl = res.data.storeUrl;
            })
            .catch((e) => {
                this.$swal({
                    icon: 'warning',
                    title: ' .',
                    text: e
                })
            })
        }
    },
    methods:{
        setStore(){
            const body = {
                islandId : Number(this.islandPos),
                storeIslandNum : Number(this.storePos),
                userId : localStorage.getItem("userId"),
                storeName : this.storeName,
                storeContent : this.storeContent,
                storeUrl : this.storeURL,
            }

            http.post("/store/regist", body)
            .then((res) => {
                if(res.status == 201){
                    this.$swal({
                        icon: 'success',
                        title: '  !',
                        text: '  !'
                    })
                }else{
                    this.$swal({
                        icon: 'error',
                        title: '  !'
                    })
                }
            })
            .catch((e) =>{
                console.log(e);
            })
            
            setTimeout(() => {
                this.$emit('open');
            }, 100);
        },
        updateStore(){
            const body = {
                storeId : this.storeId,
                storeName : this.storeName,
                storeContent : this.storeContent,
                storeUrl : this.URL,
            }

            http.put("/store/storeUpdateAll", body)
            .then(() => {
                this.cancle();
            })
            .catch((e) => {
                console.log(e)
            })
        },
        cancle(){
            this.$emit('closeStoreModal');
        }
    }
}
</script>

<style scoped>
#test{
    background-image: url("/images/Main_Background.png");
    width: 80vw;
    height: 50vw;
}
#box{
  position:absolute;
  border: 2px solid black;
  border-radius: 10px;
  left: 35%;
  top: 15%;
  width: 30vw;
  height: 40vh;
  transform: translate(-50%,10%);
  background-color: #eee;
}
.input{
  position:absolute;
  border: 2px solid black;
  border-radius: 7px;
}
.divbox{
    margin-left: 5vw;
    margin-top: 3vh;
}
.buttons{
  margin-left: 10vw;
  margin-top: 13vh;
}
.btn{
  border: 1px solid black;
  background-color: #eee;
}
</style>