<template>
  <div>
    <div class="id">
    <b>: </b> <input class="idInput" v-model="userId"/>
    <button class="btn" style="margin-left:1.5vw" @click="idValid()"><b></b></button><br>
    </div>
    <div class="pw">
    <b>: </b><input class="pwInput" type="password" v-model="userPassword"/><br>
    </div>
    <div class="ni">
    <b>: </b><input class="niInput" v-model="userNickname"/>
    <button class="btn" style="margin-left:1.5vw" @click="nicknameValid()"><b></b></button><br>
    </div>
    <div class="ph">
    <b>: </b><input class="phInput" v-model="userPhone"/><br>
    </div>
    <div class="buttons" style="margin-top:6vh">
    <button class="btn" style="margin-left:3vw; margin-right:3vw" @click="signUp()"> <b></b> </button>
    <button class="btn" @click="$emit('close-modal')"> <b></b> </button>
    </div>
  </div>
</template>
<script>
import http from '@/http.js';

export default {
  name: 'SignUp',

  data() {
    return {
      userSeq: '',
      userId: '',
      userNickname: '',
      userPhone: '',
      userToken: '',
      userPassword: '',
      valid_id : false,
      valid_nickname: false,
    };
  },
  methods: {
    signUp() {
      if(this.valid_id && this.valid_nickname){
        const body = {
        userId : this.userId,
        userPassword : this.userPassword,
        userNickname : this.userNickname,
        userPhone : this.userPhone
        }
        http.post('/user/signUp', body)
        .then(()=>{
          this.$swal({
            icon: 'success',
            title: ' !',
            text: '!   :)'
          })
          this.$emit('close-modal')
        })
      }else{
        this.$swal({
            icon: 'warning',
            title: ' .'
          })
      }
    },
    idValid(){
      http.get('/user/userId/' + this.userId)
      .then((res)=>{
        if(res.status==201){
          this.valid_id = true;
          this.$swal({
            icon: 'success',
            title: ' .'
          })
        }else{
          this.valid_id = false;
          this.$swal({
            icon: 'error',
            title: ' .',
            text: '  .'
          })
        }
      })
    },
    nicknameValid(){
      http.get('/user/userNickname/'+this.userNickname)
      .then((res)=>{
        if(res.status==201){
          this.valid_nickname = true;
          this.$swal({
            icon: 'success',
            title: ' .'
          })
        }else{
          this.valid_nickname = false;
          this.$swal({
            icon: 'error',
            title: ' .',
            text: '  .'
          })
        }
      })
    }
  },
};
</script>

<style scoped>
.id{
  margin-left: 3vw;
  margin-top: 6vh;
}
.pw{
  margin-left: 3vw;
  margin-top: 3vh;
}
.ni{
  margin-left: 3vw;
  margin-top: 3vh;
}
.ph{
  margin-left: 3vw;
  margin-top: 3vh;
}
.idInput{
  margin-left: 1.5vw;
  border: 1px solid black;
  border-radius: 7px;
}
.pwInput{
  margin-left: 0.5vw;
  border: 1px solid black;
  border-radius: 7px;
}
.niInput{
  margin-left: 1.5vw;
  border: 1px solid black;
  border-radius: 7px;
}
.phInput{
  margin-left: 0.5vw;
  border: 1px solid black;
  border-radius: 7px;
}
.buttons{
  margin-left: 5vw;
  margin-top: 2vh;
}
.btn{
  border: 1px solid black;
  background-color: #eee;
}
</style>