<template>
  <div>
      <!-- 
      
       
       
       
          -->
      
    <v-form v-model="valid" id="editModal">
    <v-container class="m-t-50" style="max-width: 800px;">
      <v-row class="justify-center">
        <v-col cols="12">
          <v-row class="justify-center">
            <v-col cols="6">
              <v-file-input
                v-model="goodsImg"
                required
                label=" "
                show-size
              ></v-file-input>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12">
          <v-row>
            <v-col >
              <v-text-field
                v-model="goodsName"
                required
                label=" "
              ></v-text-field>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12">
          <v-row>
            <v-col >
              <v-text-field
                v-model="goodsPrice"
                required
                type="number"
                label=" "
              ></v-text-field>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12">
          <v-row>
            <v-col >
              <v-text-field
                v-model="goodsQuantity"
                required
                type="number"
                label=" "
              ></v-text-field>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12">
          <v-row>
            <v-col>
              <v-textarea
                v-model="goodsContent"
                required
                label=" "
              ></v-textarea>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
    <v-container style="max-width: 800px; height: 200px;">
      <v-col>
        <v-row class="d-flex justify-content-end">
          <v-col cols="3">
            <v-btn v-if="this.isUpdate == false" block text x-large class="btn-add text-white" @click="regist">
              
            </v-btn>
            <v-btn v-else block text x-large class="btn-add text-white" @click="update">
              
            </v-btn>
          </v-col>
          <v-col cols="3">
            <v-btn block text x-large class="btn-cancle text-white" @click="cancel">
              
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-container>
  </v-form>

  </div>
</template>

<script>

import http from '@/http.js';

export default {
  name: "GoodsModal",
  props:{
    storeid: String,
    goods: Object,
    isUpdate: Boolean,
  },
  data(){
    return{
      goodsName: '',
      goodsPrice: '',
      goodsImg: null,
      goodsQuantity: '',
      goodsContent: '',
      vaild: true,
    }
  },
  mounted(){
    //      .
    if(this.isUpdate == true){
      this.goodsName = this.goods.goodsName;
      this.goodsPrice = this.goods.goodsPrice;
      this.goodsImg = null;
      this.goodsQuantity = this.goods.goodsQuantity;
      this.goodsContent = this.goods.goodsContent;
    }

  },
  methods:{
    regist(){
      if(!this.vaild){
        this.$swal({
          icon: 'warning',
          title: '   ',
        })
      }else{
        let goodsInfo = new FormData();
        goodsInfo.append('goodsName', this.goodsName);
        goodsInfo.append('goodsPrice', this.goodsPrice);
        goodsInfo.append('goodsImg', this.goodsImg);
        goodsInfo.append('goodsQuantity', this.goodsQuantity);
        goodsInfo.append('goodsContent', this.goodsContent);
        goodsInfo.append('storeId', this.storeid);
          http.post('/goods', goodsInfo, {
            headers: { 'Content-Type': 'multipart/form-data' },
          })
          .then(() => {
            this.$swal({
              icon: 'success',
              title: ' !',
            })
            this.cancel();
          })
          .catch(() => {
            this.$swal({
              icon: 'error',
              title: '  ~!',
            })
          })
        
      }
    },
    update(){
      let goodsInfo = new FormData();
        goodsInfo.append('goodsName', this.goodsName);
        goodsInfo.append('goodsPrice', this.goodsPrice);
        if(this.goodsImg != null){
          goodsInfo.append('goodsImg', this.goodsImg);
        }
        goodsInfo.append('goodsQuantity', this.goodsQuantity);
        goodsInfo.append('goodsContent', this.goodsContent);
        goodsInfo.append('storeId', this.storeid);
        goodsInfo.append('goodsId', this.goods.goodsId);
        http.put('/goods', goodsInfo, {
          headers: { 'Content-Type': 'multipart/form-data' },
        })
        .then(() => {
          this.$swal({
              icon: 'success',
              title: ' !',
            })
          this.cancel();
        })
        .catch(() => {
          this.$swal({
              icon: 'error',
              title: '  ~!',
            })
        })
    },
    cancel(){
      this.$emit('closeGoodsModal');
    }


  }

}
</script>

<style>
#editModal{
  border: 2px solid black;
  border-radius: 10px;
  background-color: #eee;
}

.btn-add{
  background-color: green;
  border-radius: 10px;
  width: 5vw;
  height: 4vh;
  color: white;
  font-weight: bold;
}

.btn-cancle{
  background-color: brown;
  border-radius: 10px;
  width: 5vw;
  height: 4vh;
  color: white;
  font-weight: bold;
}

</style>